<template>

  <Row>


    <Col :xs="{span:20,offset:1} " :sm="{span:8,offset:8}" :md="{span:8,offset:8}" :lg="{span:8,offset:8}" style="width: 342px" >

      <Card class="main">
        <div  slot="title" style="height: auto">
      <Row>
        <Col span="8"  ><div style="width: auto"><img
          :src="'static/img/tou01.png'" style="width: 70%"
          alt=""></div>
        </Col >
        <Col span="14" :offset="2">
          <Input id="name" prefix="ios-contact" placeholder="name" style="width: auto;margin-bottom: 20px;" disabled="true"/>
          <Input prefix="md-grid" placeholder="id" style="width: auto;margin-bottom: 20px;" disabled="true" />
        </Col >
      </Row>
        </div>
        <Button  @click="value1 = true" long style="margin-bottom: 20px;padding: 10px;font-family: 黑体" type="primary">个人信息</Button>
        <Drawer :closable="false" width="70%" v-model="value1">
          <p :style="pStyle">个人信息</p>
          <p :style="pStyle">基本信息</p>
          <div class="demo-drawer-profile">
            <Row>
              <Col span="12">
               用户名: Aresn
              </Col>
              <Col span="12">
                id: xxx
              </Col>
            </Row>
            <Row>
              <Col span="12">
                城市: BeiJing
              </Col>
              <Col span="12">
                国家: China
              </Col>
            </Row>
            <Row>
              <Col span="12">
                生日: May 14, 1991
              </Col>
              <Col span="12">
               邮箱: qq.com
              </Col>
            </Row>
            Message: Hello, 用户名
          </div>
          <Divider />



        </Drawer>
        <Button  long style="margin-bottom: 20px;padding: 10px;font-family: 黑体;" @click="value2 = true" type="primary">设置</Button>
        <Drawer title="设置：" :closable="false" v-model="value2" style="font-family: 华文行楷;">
          <div v-for="(item,index) in 6" style="margin-top: 15px">
            <span style="font-family: 楷体;font-size: 16px">功能{{index}}：</span>
            <Button @click="swithon(index)" style="margin-left: 5px;font-size: 16px" type="success">开启
          </Button> <Button @click="swithdown(index)" style="margin-left: 5px;font-size: 16px" type="error">关闭</Button>
          </div>



        </Drawer>




        <Button  @click="modal6 = true"  long style="margin-bottom: 20px;padding: 10px;font-family: 黑体" type="primary">检查更新</Button>
        <Modal
          v-model="modal6"
          title="检查更新"
          :loading="loading"
          @on-ok="asyncOK">
          <p v-show="isjcok1">点击确定开始检查</p>
          <p v-show="isjcok2">{{message}}</p>
        </Modal>
        <p-footer></p-footer>
      </Card>
    </Col>
  </Row>

</template>

<script>
  import Footer from '@/components/footer'
    export default {
        name: "Home",
      components: {
        'p-footer': Footer
      },
      data () {
        return {
          value2: false,
          value1: false,
          modal6: false,
          loading: true,
          isjcok1:true,
          isjcok2:false,
          message:'检查中...',
          switch1: false,
          pStyle: {
            fontSize: '16px',
            color: 'rgba(0,0,0,0.85)',
            lineHeight: '24px',
            display: 'block',
            marginBottom: '16px'
          }
        }
      },
      methods: {
        asyncOK () {
          setTimeout(() => {
            this.isjcok1 = false;
            this.isjcok2 = true;

          }, 100);
          setTimeout(() => {
            this.message = '已经为最新版本';

          }, 2500);
          setTimeout(() => {
            this.modal6 = false;
          }, 4000);
          setTimeout(() => {
            this.message = '点击确定开始检查';
          }, 4500);
        },
        change (status) {
          this.$Message.info('开关状态：' + status);
        },
        swithon(num){
          this.$Message.info('功能：' +num + ' 开启')
        },
        swithdown(num){
          this.$Message.info('功能：' +num + ' 关闭')
        }
      }
    }
</script>

<style>
  .demo-drawer-profile{
    font-size: 14px;
  }
  .demo-drawer-profile .ivu-col{
    margin-bottom: 12px;
  }
  .main{height: 471px;overflow: hidden}
</style>



